import React, { Component } from 'react';
import './index.css';
import {Router, Link} from '@reach/router';

class Tax extends Component {
    constructor (props){
        super(props);
        this.state = {
          taxin: 1,
          taxout: 1,
          tps: 0.05,
          tvq: 0.09975
        };
        this.handleChange = this.handleChange.bind(this);
        this.taxoutChange = this.taxoutChange.bind(this);
      }
    
      handleChange(e){
        var tps = (e.target.value * (this.state.tps));
        console.log("tps = " + tps);
    
        var tvq = (e.target.value * (this.state.tvq));
        console.log("tvq =" + tvq);
        var total = (parseFloat(e.target.value) + parseFloat(tps) + parseFloat(tvq));
        console.log(parseFloat(tps));
        console.log(parseFloat(tvq));
        console.log(total);
        total = total || 0;
        this.setState({
          taxin: total.toFixed(2),
          taxout: e.target.value
        });
      }
    
      taxoutChange(e){
        var tps = (e.target.value * (this.state.tps));
        console.log("valeur tps " + tps);
    
        var tvq = (e.target.value * (this.state.tvq));
        console.log("valeur tvq" + tvq);
        var total = (parseFloat(e.target.value) / 1.14975);
        total = total || 0
        this.setState({
          taxin: e.target.value,
          taxout: total.toFixed(2)
    
        });
      }
    
    

      
    render(){
        return(
        <>
         <header className="App-header">
          <label htmlFor="taxout">Sans taxes :</label>
          <input type="number" id="taxout" name="taxout" value={this.state.taxout} onChange={this.handleChange}></input>
          <label htmlFor="taxin">Tax In (TPS et TVQ):</label>
          <input type="number" id="taxin" name="taxin" value={this.state.taxin} onChange={this.taxoutChange}></input>
        </header>
        </>
        )
      }
    }

export default Tax;
